import { Box, Typography, Menu, MenuItem, styled } from "@mui/material";
import { useState } from "react";
import PowerSettingsNewIcon from "@mui/icons-material/PowerSettingsNew";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { DataContext } from "../../context/DataProvider";
import { authenticateLogout } from "../../service/api";

const Component = styled(Menu)`
  margin-top: 5px;
`;
const Logout = styled(Typography)`
  font-size: 14px;
  margin-left: 20px;
`;

const Profile = ({ account, setAccount }) => {
  const [open, setOpen] = useState(false);

  // const handleClick = (event) => {
  //   setOpen(event.currentTarget);
  // };

  // const handleClose = () => {
  //   setOpen(false);
  // };

  // const logoutUser = () => {
  //   setAccount("");
  // };

  const { dispatch, userData } = useContext(DataContext);

  //console.log(userData);

  const navigate = useNavigate();

  const handleClick = (e) => {
    setOpen(e.currentTarget);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const logoutUser = async () => {
    //setAccount('');
    let response = await authenticateLogout();
    //console.log(response);
    if (response.status === 200) {
      handleClose();
      dispatch({ type: "USER", payload: false });
      navigate("/", { replace: true });
      alert(`${response?.data} Thanks for Shopping Us !`);
    }
  };

  const { firstname } = userData;

  // const profileDetails = () => {
  //   navigate("/profile");
  // };

  return (
    <>
      <Box onClick={handleClick} style={{ display: "flex", cursor: "pointer" }}>
        <Typography style={{ marginTop: 2 }}>{firstname}</Typography>
        <KeyboardArrowDownIcon style={{ marginInline: 5, width: 18 }} />
      </Box>

      <Component anchorEl={open} open={Boolean(open)} onClose={handleClose}>
        <MenuItem
          onClick={() => {
            handleClose();
            logoutUser();
          }}
        >
          <PowerSettingsNewIcon color="primary" fontSize="small" />
          <Logout>Logout</Logout>
        </MenuItem>
      </Component>
    </>
  );
};

export default Profile;
