import * as actionTypes from '../constants/cartConstant';

export const cartReducer = (state = { cartItems: []}, action) => {
    switch(action.type) {
        case actionTypes.ADD_TO_CART:
            const item = action.payload;

            const existItem = state.cartItems.find(product => product.id === item.id);
            
            if(existItem){
                return {
                    ...state, cartItems: state.cartItems.map(product => product.id === existItem.id ? item : product)
                }
            } else {
                return  { ...state, cartItems: [...state.cartItems, item]}
            }
        case actionTypes.REMOVE_FROM_CART:
            return {
                ...state, cartItems: state.cartItems.filter(product => product.id !== action.payload)
            }

            case actionTypes.GET_ORDER_DETAILS:
                const order = action.payload;
                return { ...state, orderDetails:{...state.orderDetails,order}}
        default:
            return state;
    }
}