import { Box, Button, Typography, Badge, styled } from "@mui/material";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import LoginDialog from "../login/LoginDialog";
import { useState, useContext } from "react";
import { DataContext } from "../../context/DataProvider";
import Profile from "./Profile";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";

const Wrapper = styled(Box)(({ theme }) => ({
  margin: "0 3% 0 auto",
  display: "flex",
  "& > button, & > p, & > div": {
    marginRight: "40px !important",
    fontSize: 15,
    alignItems: "center",
  },
  [theme.breakpoints.down("md")]: {
    color: "#2874f0",
    // display: "flex",
    // flexDirection: "column",
    // alignItems: "center",
    display: "block",
    marginTop: 10,
  },
  // [theme.breakpoints.down("md")]: {
  //   display: "block",
  // },
}));

const Container = styled(Link)(({ theme }) => ({
  display: "flex",
  textDecoration: "none",
  color: "inherit",
  [theme.breakpoints.down("md")]: {
    display: "block",
  },
}));

const LogInButton = styled(Button)`
  background: #fff;
  color: #2874f0;
  text-transform: none;
  padding: 5px 40px;
  border-radius: 1px;
  box-shadow: none;
  font-weight: 600;
  height: 31px;
`;

const CustomButtons = () => {
  const [open, setOpen] = useState(false);
  const { account, setAccount, state } = useContext(DataContext);

  const { cartItems } = useSelector((state) => state.cart);

  const openDialog = () => {
    setOpen(true);
  };

  return (
    <Wrapper>
      {state ? (
        <Profile account={account} setAccount={setAccount} />
      ) : (
        <LogInButton variant="contained" onClick={() => openDialog()}>
          Login
        </LogInButton>
      )}

      <Typography style={{ marginTop: 3, width: 135, fontWeight: 600 }}>
        Become a Seller
      </Typography>
      <Box style={{ display: "flex", cursor: "pointer", marginTop: 0 }}>
        <Typography style={{ fontSize: 15, fontWeight: 600 }}>More</Typography>
        <KeyboardArrowDownIcon />
      </Box>

      <Container to="/cart" style={{ marginTop: 3 }}>
        <Badge badgeContent={cartItems?.length} color="secondary">
          <ShoppingCartIcon />
        </Badge>

        <Typography style={{ marginLeft: 10, fontSize: 15, fontWeight: 600 }}>
          Cart
        </Typography>
      </Container>
      <LoginDialog open={open} setOpen={setOpen} />
    </Wrapper>
  );
};

export default CustomButtons;
